/*
 * Multi step (wizard) checkout. Each store should implement and register its own implementation.
 */
import {
  CHECKOUT_STEP_FINISHED,
  CHECKOUT_JUMP_TO_STEP,
  CHECKOUT_SEND_STEP_DATA,
  CHECKOUT_PORT_NAME
} from 'const'

const register = {}

export default class Checkout {
  static getCheckoutStoreClass (store) {
    return register[store]
  }

  static getCheckoutStore (order, store) {
    return new register[store](order)
  }

  static getProductData (domain, productId) {
    throw Error('Not implemented')
  }

  static registerStore (name, store) {
    register[name] = store
  }

  constructor (order, site) {
    this.current_site = site
    this.order = order
    this.stepNum = 0
    this.step = this.steps[this.stepNum]
  }

  start () {
    chrome.tabs.create(
      {
        url: this.checkoutUrl
      },
      tab => {
        this.tab = tab
        const listener = (tabId, changeInfo) => {
          if (tabId !== tab.id) return
          if (changeInfo.status === 'complete') this.onLoad()
        }

        chrome.tabs.onUpdated.addListener(listener)
      }
    )
  }

  get steps () {
    throw Error('Not implemented')
  }

  get blockCheckout () {
    return false
  }

  sendStepData () {
    this.port.postMessage({
      step: this.step,
      data: this.stepData()
    })
  }

  onLoad () {
    if (this.isLastStep()) {
      this.port = this.tab = null
      return
    }

    this.port = chrome.tabs.connect(
      this.tab.id,
      { name: CHECKOUT_PORT_NAME }
    )
    this.port.onMessage.addListener(this.externalListener.bind(this))
    this.sendStepData()
  }

  isLastStep () {
    return this.steps.indexOf(this.step) === this.steps.length - 1
  }

  externalListener ({ topic, data }) {
    switch (topic) {
      case CHECKOUT_STEP_FINISHED:
        this.stepFinished()
        break
      case CHECKOUT_JUMP_TO_STEP:
        this.jumpToStep(data)
        break
      case CHECKOUT_SEND_STEP_DATA:
        this.sendStepData()
        break
    }
  }

  stepFinished () {
    this.step = this.steps[++this.stepNum]
  }

  jumpToStep ({ step }) {
    const stepIndex = this.steps.indexOf(step)
    this.stepNum = stepIndex
    this.step = this.steps[stepIndex]
  }

  stepData () {
    throw Error('Not implemented')
  }
}
