import { SYNC_TRACKING_PORT_NAME } from 'const'

const register = {}

export default class SyncTrackingNumbers {
  static getSyncTrackingNumbersStore (store, orderId, storeOrderId) {
    return new register[store](orderId, storeOrderId)
  }

  static registerStore (name, store) {
    register[name] = store
  }

  constructor (orderId) {
    this.orderId = orderId
  }

  start () {
    chrome.tabs.create({ url: this.syncUrl }, tab => {
      this.tab = tab
      chrome.tabs.onUpdated.addListener((tabId, changeInfo) => {
        if (tabId === tab.id && changeInfo.status === 'complete') {
          this.onLoad()
        }
      })
    })
  }

  onLoad () {
    this.port = chrome.tabs.connect(
      this.tab.id,
      { name: SYNC_TRACKING_PORT_NAME }
    )
    this.port.onMessage.addListener(this.externalListener.bind(this))
    this.port.postMessage({
      data: this.syncData()
    })
  }

  externalListener ({ topic }) {
    switch (topic) {
      case 'sync_finished':
        this.port = this.tab = null
        break
    }
  }

  syncData () {
    throw Error('Not implemented')
  }
}
