import { log } from '../analytics'
import * as Sentry from '@sentry/browser'

export function captureError (store, message, error, data) {
  log(store, message, { error, data })
  Sentry.configureScope((s) => {
    s.setExtra('store', store)
    if (error) {
      s.setExtra('response', {
        error: error
      })
    }
    if (data) {
      s.setExtra('data', data)
    }
  })
  Sentry.captureMessage(message, Sentry.Severity.Error)
}
