const MODALYST_ERRORS = {
  ALREADY_IMPORTED: 'This product is already in the inventory',
  OVER_THE_LIMIT: 'Item limit for the subscription plan has been reached'
}

/**
 * It's tricky to get all the errors from response. This method will iterate over all field and non-field errors
 * and return them in a flat list of error messages.
 * @param error
 * @returns {any[]}
 */
const getErrorsArray = (error) => {
  if (error.response && error.response.data && error.response.data.errors) {
    return Object.values(error.response.data.errors).flat(Infinity)
  }
}

export {
  MODALYST_ERRORS,
  getErrorsArray
}
