/*
 * Checks if user is authenticated in Modalyst using Modalyst API.
 * Notifies tabs when users sign in or out.
 */
import { checkAuth, urlBase } from 'common/modalyst_api'
import flatMap from 'lodash/flatMap'
import availableSites from 'available_sites'
import { BACKGROUND_EVENTS } from 'const'

const urlMatchers = flatMap(availableSites, 'url_matchers')

function notifyTabs (username, stores, csrftoken) {
  const authenticated = !!username && !!csrftoken

  chrome.tabs.query({ url: urlMatchers }, tabs => {
    for (const tab of tabs) {
      chrome.tabs.sendMessage(tab.id, {
        topic: BACKGROUND_EVENTS.AUTHENTICATED,
        data: { authenticated, username, stores, csrftoken }
      })
    }
  })
}

function authenticationFailure () {
  chrome.storage.local.set({ username: false })
  notifyTabs(false)
}

function authenticationSuccess (data) {
  const username = data.username || data.data.username
  const stores = data.stores || data.data.stores
  chrome.storage.local.set({ username })
  chrome.cookies.get({ url: urlBase, name: 'csrftoken' }, cookie =>
    notifyTabs(username, stores, cookie && cookie.value)
  )
}

export default function authenticate () {
  chrome.storage.local.get('username', ({ username }) => {
    if (username) {
      authenticationSuccess({ username, stores: {} })
      checkAuth().then(authenticationSuccess, authenticationFailure)
    } else {
      checkAuth().then(authenticationSuccess, authenticationFailure)
    }
  })
}
