/*
 * Creates item in Modalyst and returns info for publishing an event when finished.
 */
import includes from 'lodash/includes'
import { PRODUCT_IMPORT_SUCCEEDED, PRODUCT_IMPORT_ERROR } from 'const'
import { createSupplierItem } from 'common/modalyst_api'
import { MODALYST_ERRORS, getErrorsArray } from 'common/modalyst_api/errors'
import { ERRORS } from './const'

/**
 * @param product
 * @param store
 * @returns {Promise<{id: void, status: string} | {id: *, error: any, status: string}>}
 */
function createItem (product, store) {
  return createSupplierItem(product.id, store)
    .then(() => ({ status: PRODUCT_IMPORT_SUCCEEDED, id: product.id }))
    .catch(error => {
      const errors = getErrorsArray(error)
      if (includes(errors, MODALYST_ERRORS.ALREADY_IMPORTED)) {
        return { status: PRODUCT_IMPORT_ERROR, id: product.id, error: ERRORS.CONFLICT }
      }
      if (includes(errors, MODALYST_ERRORS.OVER_THE_LIMIT)) {
        return { status: PRODUCT_IMPORT_ERROR, id: product.id, error: ERRORS.LIMIT_EXCEEDED }
      }
      return { status: PRODUCT_IMPORT_ERROR, id: product.id, error }
    })
}

export default createItem
