/*
 * Background page script
 * Listens to BACKGROUND_EVENTS and reacts/responds.
 * Controls Page Action (Chrome feature) availability.
 */

import Checkout from './checkout'
import SyncTrackingNumbers from './sync_tracking_numbers'
import authenticate from './auth'
import { BACKGROUND_EVENTS } from 'const'
import registerCheckouts from 'stores/register_checkouts'
import registerSyncTrackingNumbers from 'stores/register_sync_tracking_numbers'
import some from 'lodash/some'
import { getStoreNameByUrl } from 'utils'
import { log, sentryInit } from '../analytics'
import { captureError } from './utils'
import createItem from '../content_script_core/create_item'

chrome.runtime.onStartup.addListener(function () {
  chrome.storage.local.clear()
})

sentryInit()
registerCheckouts()
registerSyncTrackingNumbers()

const checkouts = []
let syncTrackingNumbers = null

function startCheckout (order, store) {
  chrome.storage.local.clear()

  const checkout = Checkout.getCheckoutStore(order, store)
  checkout.start()
  checkouts.push(checkout)
}

function startSyncTrackingNumbers (store, orderId, storeOrderId) {
  syncTrackingNumbers = SyncTrackingNumbers.getSyncTrackingNumbersStore(
    store,
    orderId,
    storeOrderId
  )
  syncTrackingNumbers.start()
}

function shouldBlockCheckoutTab (tabId) {
  return some(checkouts, ({ tab, blockCheckout }) => tab && String(tab.id) === String(tabId) && blockCheckout)
}

function getProductData (store, domain, productId) {
  const klass = Checkout.getCheckoutStoreClass(store)
  return klass.getProductData(domain, productId)
}

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
  switch (request.topic) {
    case BACKGROUND_EVENTS.START_CHECKOUT:
      startCheckout(request.order, request.store)
      break
    case BACKGROUND_EVENTS.AUTHENTICATE:
      authenticate()
      break
    case BACKGROUND_EVENTS.SYNC_TRACKING_NUMBERS:
      startSyncTrackingNumbers(request.store, request.orderId, request.storeOrderId)
      break
    case BACKGROUND_EVENTS.CHECKOUT_LOCK:
      sendResponse({ setLock: shouldBlockCheckoutTab(sender.tab.id) })
      break
    case BACKGROUND_EVENTS.IMPORT_ITEM:
      createItem(request.item, request.store).then((response) => sendResponse(response))
      return true
    case BACKGROUND_EVENTS.GET_EXTERNAL_PRODUCT_INFO:
      getProductData(request.store, request.domain, request.productId).then((response) => sendResponse(response.data))
      return true //  If you want to asynchronously use sendResponse, add return true; to the onMessage event handler. (from extension docs)
    case BACKGROUND_EVENTS.CAPTURE_ERROR:
      captureError(request.store, request.message, request.error, request.data)
      break
    case BACKGROUND_EVENTS.LOG_EVENT:
      log(request.store, request.message, request.data)
      break
  }
})

chrome.tabs.onUpdated.addListener((tabId, changeInfo, tab) => {
  if (changeInfo.status === 'complete' && getStoreNameByUrl(tab.url)) {
    chrome.pageAction.show(tabId)
  }
})
