import * as Sentry from '@sentry/browser'
import appConfig from 'app-config.json'
import { logEvent } from 'common/modalyst_api'
import { bindEvents, getContext } from 'utils'
import {
  AUTH_SUCCESS,
  AUTH_FAILURE,
  START_PRODUCT_IMPORT,
  PRODUCT_IMPORT_SUCCEEDED,
  PRODUCT_IMPORT_ERROR,
  CHECKOUT_ERROR,
  START_SYNC,
  SYNC_SUCCESS,
  SYNC_ERROR,
  CHECKOUT_STEP_STARTED,
  BACKGROUND,
  CONTENT, BACKGROUND_EVENTS
} from 'const'

export function sentryInit () {
  Sentry.init({
    dsn: appConfig.sentry_dsn,
    release: appConfig.version,
    beforeSend: (event) => {
      // ignore Yandex Browser or anything that doesn't include Chrome in UA
      if (event.request && event.request.headers && event.request.headers['User-Agent']) {
        const ua = event.request.headers['User-Agent']
        if (ua.includes('YaBrowser') || !ua.includes('Chrome')) {
          return null
        }
      }
      return event
    }
  })
}

const ModalystBackend = (store, message, _userId, data) => {
  const context = getContext()
  if (context === CONTENT) {
    chrome.runtime.sendMessage({
      topic: BACKGROUND_EVENTS.LOG_EVENT,
      store,
      message,
      data
    })
  } else if (context === BACKGROUND) {
    logEvent(
      data
        ? `[${appConfig.version}][${store}] ${message} [${JSON.stringify(data)}]`
        : `[${appConfig.version}][${store}] ${message}`
    )
  } else {
    console.error(`Unknown context: ${context}`)
  }
}

const ConsoleBackend = (store, message, _userId, data) => {
  console.log(`[${appConfig.version}][${store}] ${message}`, data)
}

const backends = [ConsoleBackend, ModalystBackend]

let userId = null

export function log (store, message, data) {
  try {
    backends.forEach(backend => backend(store, message, userId, data))
  } catch (e) {
    console.error('Logging failed: ', e)
  }
}

export function setupAnalytics (store) {
  bindEvents({
    [AUTH_SUCCESS]: ({ username }) => {
      userId = username
    },
    [AUTH_FAILURE]: () => {
      userId = null
    }
  })

  bindEvents({
    [START_PRODUCT_IMPORT]: productId => log(store, 'import button clicked', { productId }),
    [PRODUCT_IMPORT_SUCCEEDED]: productId => log(store, 'product imported', { productId }),
    [PRODUCT_IMPORT_ERROR]: data => log(store, 'product import failed', data),
    [CHECKOUT_STEP_STARTED]: data => log(store, `new checkout step started - ${data.step}`, data.data),
    [CHECKOUT_ERROR]: () => log(store, 'checkout unknown error'),
    [START_SYNC]: () => log(store, 'syncing started'),
    [SYNC_SUCCESS]: data => log(store, 'syncing finished', data),
    [SYNC_ERROR]: data => log(store, 'syncing error', data)
  })
}
