import axios from 'axios'
import { bindEvents } from 'utils'
import { AUTH_SUCCESS } from 'const'
import appConfig from 'app-config.json'

const urlBase = appConfig.root_url

const axiosInstance = axios.create({
  baseURL: urlBase
})

function checkAuth () {
  return axiosInstance.get('/api/v2/auth/')
}

function updateOrder (orderId, ...data) {
  return axiosInstance.put(`/api/v2/orders/${orderId}/`, data)
}

function updateOrderItem (orderId, itemId, data) {
  return axiosInstance.put(`/api/v2/orders/${orderId}/items/${itemId}/`, data)
}

function orderByWishId (id) {
  return axiosInstance.get(`/api/orders/wish/${id}/`)
}

function orderById (id) {
  return axiosInstance.get(`/api/v2/orders/${id}/`)
}

function createFullExtensionItem (data) {
  return axiosInstance.post('/api/v3/items/', data)
}

function logEvent (message) {
  return axiosInstance.post('/api/v3/logs/', { message })
}

function createSupplierItem (productId, source) {
  return axiosInstance.post('/api/v3/trigger_product_pull/', { external_id: productId, source })
}

bindEvents({
  [AUTH_SUCCESS]: ({ csrftoken }) => {
    axiosInstance.defaults.headers.common['X-CSRFToken'] = csrftoken
  }
})

export {
  urlBase,
  checkAuth,
  updateOrder,
  updateOrderItem,
  orderByWishId,
  orderById,
  createFullExtensionItem,
  logEvent,
  createSupplierItem
}
